import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {EpicsService} from './redux/epics/epics.service';
import {EpicsModule} from './redux/epics/epics.module';
import {NgReduxRouterModule} from '@angular-redux/router';
import {DevToolsExtension, NgRedux, NgReduxModule} from '@angular-redux/store';
import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {NzLayoutModule} from 'ng-zorro-antd/layout';
import {NzMenuModule} from 'ng-zorro-antd/menu';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {ImLoginUserService} from './services/im-login-user.service';
import {AuthInterceptor} from './services/auth-interceptor.service';
import {IconsProviderModule} from './icons-provider.module';
import {AppState} from './redux';
import {createEpicMiddleware} from 'redux-observable';
import {reducers} from './redux/reducers';
import {createLogger} from 'redux-logger';
import {NzIconModule} from 'ng-zorro-antd/icon';
import { ImLoginComponent } from './component/im-login/im-login.component';
import {NzModalModule} from 'ng-zorro-antd/modal';
import {NzButtonModule} from 'ng-zorro-antd/button';
import {NzInputModule} from 'ng-zorro-antd/input';
import { ImMenuComponent } from './component/im-menu/im-menu.component';
import { ImOptionsStoreComponent } from './component/options/im-options-store.component';
import {NzTabsModule} from 'ng-zorro-antd/tabs';
import {ImCreateUserComponent} from './component/options/im-users-administration/im-create-user/im-create-user.component';
import {ImUsersAdministrationComponent} from './component/options/im-users-administration/im-users-administration.component';
import {NzGridModule} from 'ng-zorro-antd/grid';
import {NzSelectModule} from 'ng-zorro-antd/select';
import { NZ_I18N, en_US } from 'ng-zorro-antd/i18n';
import {CategoriesComponent} from './component/options/im-users-administration/categories/categories.component';
import {NzTreeModule} from 'ng-zorro-antd/tree';
import {NzDropDownModule} from 'ng-zorro-antd/dropdown';
import {NzPopoverModule} from 'ng-zorro-antd/popover';
import {NzFormModule} from 'ng-zorro-antd/form';

const LOGGER_CONFIG = {
  diff: true
};


@NgModule({
  declarations: [
    AppComponent,
    ImLoginComponent,
    ImMenuComponent,
    ImOptionsStoreComponent,
    ImCreateUserComponent,
    ImUsersAdministrationComponent,
    CategoriesComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    NzLayoutModule,
    NzMenuModule,
    NzModalModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    EpicsModule,
    NgReduxRouterModule.forRoot(),
    NgReduxModule,
    IconsProviderModule,
    NzIconModule,
    NzButtonModule,
    NzInputModule,
    NzTabsModule,
    NzGridModule,
    NzSelectModule,
    NzTreeModule,
    NzDropDownModule,
    NzPopoverModule,
    NzFormModule,
  ],
  providers: [
    {provide: NZ_I18N, useValue: en_US},
    EpicsService,
    ImLoginUserService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
  constructor(private ngRedux: NgRedux<AppState>,
              private devTools: DevToolsExtension,
              private epicsService: EpicsService) {
    const epics = this.epicsService.getEpics();
    const middleware = createEpicMiddleware();
    let enhancers = [];
    if (devTools.isEnabled()) {
      enhancers = [devTools.enhancer()];
    }
    const logger = createLogger(LOGGER_CONFIG);
    ngRedux.configureStore(reducers, {} as AppState, [middleware, logger], enhancers);
    middleware.run(epics);
  }
}
