import {AllLoginAction, LoginUserActionEnum} from './login.actions';
import {LoginUserModel} from '../../model/im-user.model';

const INITIAL_STATE: LoginUserModel = {
  role: null,
  id: null,
  displayName: null,
  token: null
};

type LoginUserAction = AllLoginAction;

export const loginUserReducer:
    (state: LoginUserModel, action: LoginUserAction) => LoginUserModel = (state = INITIAL_STATE, action: LoginUserAction) => {
    switch (action.type) {
      case LoginUserActionEnum.LOGIN_USER_SUCCESS: {
        const {displayName, role, token, id} = action.payload;
        return {
          displayName, role, token, id
        };
      }
      case LoginUserActionEnum.LOGOUT_USER_SUCCESS: {
        return INITIAL_STATE;
      }
      default: {
        return state;
      }
    }
  }
;
