import {ChangeDetectionStrategy, Component, Input, OnInit} from '@angular/core';
import {select} from '@angular-redux/store';
import {checkLoginSelector} from '../../redux/login/login.selector';
import {Observable} from 'rxjs';

@Component({
  selector: 'im-menu',
  templateUrl: './im-menu.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./im-menu.component.less']
})
export class ImMenuComponent implements OnInit {
  @select(checkLoginSelector) checkLogin$: Observable<boolean>;
  @Input() isCollapsed = false;
  @Input() modelMenu;

  constructor() {
  }

  ngOnInit() {
  }

  createRouter(mainRout: string, subRout?: string) {
    return !!subRout ? (!!mainRout ? `/${mainRout}/${subRout}` : `/${subRout}`) : `/${mainRout}`;
  }

}
