import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {NgRedux, select} from '@angular-redux/store';
import {AppState} from '../../redux';
import {loginUserAction, logoutUserAction} from '../../redux/login/login.actions';
import {checkLoginSelector} from '../../redux/login/login.selector';
import {Observable} from 'rxjs';

@Component({
  selector: 'im-login',
  templateUrl: './im-login.component.html',
  styleUrls: ['./im-login.component.less']
})
export class ImLoginComponent implements OnInit {
  @select(checkLoginSelector) checkLogin$: Observable<boolean>;

  constructor(private ngRedux: NgRedux<AppState>) {
  }

  isVisible = false;
  loginForm: FormGroup;
  isLogin = false;

  ngOnInit() {
    this.loginForm = new FormGroup({
      user: new FormControl('', [Validators.required, Validators.minLength(3)]),
      password: new FormControl('', [Validators.required, Validators.minLength(3)])
    });
    this.checkLogin$.subscribe(isLogin => this.isLogin = isLogin);
  }

  showModal1(): void {
    this.isVisible = true;
  }

  handleOk(): void {
    if (!this.isLogin) {
      this.ngRedux.dispatch(loginUserAction(this.loginForm.getRawValue()));
    } else {
      this.ngRedux.dispatch(logoutUserAction());
    }

    this.isVisible = false;
  }

  handleCancel(): void {
    this.isVisible = false;
  }
}
