import {ChangeDetectorRef, Component, OnInit} from '@angular/core';
import {FormControl, FormGroup} from '@angular/forms';
import {NgRedux} from '@angular-redux/store';
import {createUserAction} from 'src/app/redux/login/login.actions';
import {USERS_ROLE} from '../../../../static/constants';
import {AppState} from 'src/app/redux';


@Component({
  selector: 'im-create-user',
  templateUrl: './im-create-user.component.html',
  styleUrls: ['./im-create-user.component.less']
})
export class ImCreateUserComponent implements OnInit {

  isVisible = false;
  form: FormGroup;
  roleList = [USERS_ROLE.ROOT_ADMIN, USERS_ROLE.MANAGER, USERS_ROLE.USER];

  constructor(private cdr: ChangeDetectorRef,
              private ngRedux: NgRedux<AppState>) {
  }

  ngOnInit() {
    this.form = new FormGroup({
      displayName: new FormControl(''),
      user: new FormControl(''),
      password: new FormControl(''),
      role: new FormControl('')
    });
  }

  showModal(): void {
    this.isVisible = true;
  }

  handleOk(): void {
    const form = this.form.getRawValue();
    this.ngRedux.dispatch(createUserAction(form));
    this.isVisible = false;
  }

  handleCancel(): void {
    this.isVisible = false;
  }

}
