import {AppState} from '../index';
import {USERS_ROLE} from '../../static/constants';

export const userSelector = (state: AppState) => state.loginUser;
export const userRoleSelector = (state: AppState) => userSelector(state).role;
export const userDisplayNameSelector = (state: AppState) => userSelector(state).displayName;
export const checkRoleAdminSelector = (state: AppState) =>
  userSelector(state).token === sessionStorage.getItem('token') && userRoleSelector(state) === USERS_ROLE.MANAGER;

export const checkLoginSelector = (state: AppState) => !!sessionStorage.getItem('token') && !!userSelector(state).token &&
  userSelector(state).token === sessionStorage.getItem('token');
export const userIdSelector = (state: AppState) => userSelector(state).id;
