import {Injectable} from '@angular/core';
import {NgRedux} from '@angular-redux/store';
import {AppState} from '../index';
import {ActionsObservable} from 'redux-observable';
import {catchError, mergeMap, switchMap} from 'rxjs/operators';
import {of} from 'rxjs';
import {
  AllLoginAction,
  createUserSuccessAction,
  LoginUserActionEnum,
  loginUserSuccessAction,
  logoutUserSuccessAction
} from './login.actions';
import {ImLoginUserService} from '../../services/im-login-user.service';

@Injectable()
export class LoginUserEpic {
  readonly allEpic;

  constructor(private ngRedux: NgRedux<AppState>,
              private imLoginUserService: ImLoginUserService) {
    this.allEpic = [
      this.loginMarket$,
      this.createUser$,
      this.checkUserToken$,
      this.logoutMarket$
    ];
  }

  loginMarket$ = (action$: ActionsObservable<AllLoginAction>) => {
    return action$.ofType(LoginUserActionEnum.LOGIN_USER).pipe(
      switchMap((userForm) => {
        const {user} = userForm.payload;
        return this.imLoginUserService.userLogin(user)
          .pipe(mergeMap(result => {
              this.imLoginUserService.addHeaderInSessionStorage(result.token);
              return of(loginUserSuccessAction(result));
            }),
            catchError(err => {
              console.warn(err, '--> Ошибка BE userLogin');
              return of();
            }));
      })
    );
  };

  logoutMarket$ = (action$: ActionsObservable<AllLoginAction>) => {
    return action$.ofType(LoginUserActionEnum.LOGOUT_USER).pipe(
      switchMap((userForm) => {
        return this.imLoginUserService.userLogout()
          .pipe(mergeMap(result => {
              this.imLoginUserService.removeHeaderInSessionStorage();
              return of(logoutUserSuccessAction());
            }),
            catchError(err => {
              console.warn(err, '--> Ошибка BE userLogin');
              return of();
            }));
      })
    );
  };

  createUser$ = (action$: ActionsObservable<AllLoginAction>) => {
    return action$.ofType(LoginUserActionEnum.CREATE_USER).pipe(
      switchMap((userForm) => {
        const {user} = userForm.payload;
        return this.imLoginUserService.createUser(user)
          .pipe(mergeMap(result => {
              return of(createUserSuccessAction(result));
            }),
            catchError(err => {
              console.warn(err, '--> Ошибка BE createLogin');
              return of();
            }));
      })
    );
  };

  checkUserToken$ = (action$: ActionsObservable<AllLoginAction>) => {
    return action$.ofType(LoginUserActionEnum.CHECK_USER_TOKEN).pipe(
      switchMap((data) => {
        const {token} = data.payload;
        if (!token) {
          return of();
        }
        return this.imLoginUserService.checkUser(token)
          .pipe(mergeMap(result => {

              return of(loginUserSuccessAction(result));
            }),
            catchError(err => {
              console.warn(err, '--> Ошибка BE checkUserToken');
              return of();
            }));
      })
    );
  };
}
