import {AppAction} from '../action.type';
import {LoginUserModel, UserModel} from '../../model/im-user.model';

export enum LoginUserActionEnum {
  LOGIN_USER = '[Login] user',
  LOGIN_USER_SUCCESS = '[Login] user success',
  LOGOUT_USER = '[Logout] user',
  LOGOUT_USER_SUCCESS = '[Logout] user success',
  CREATE_USER = '[Create] user',
  CREATE_USER_SUCCESS = '[Create] user success',
  CHECK_USER_TOKEN = '[Check] token user'
}

export const loginUserAction = (user: UserModel): AppAction<LoginUserActionEnum.LOGIN_USER> => ({
  type: LoginUserActionEnum.LOGIN_USER,
  payload: {user}
});

export const loginUserSuccessAction = (user: LoginUserModel): AppAction<LoginUserActionEnum.LOGIN_USER_SUCCESS> => ({
  type: LoginUserActionEnum.LOGIN_USER_SUCCESS,
  payload: user
});

export const logoutUserAction = (): AppAction<LoginUserActionEnum.LOGOUT_USER> => ({
  type: LoginUserActionEnum.LOGOUT_USER,
  payload: {}
});

export const logoutUserSuccessAction = (): AppAction<LoginUserActionEnum.LOGOUT_USER_SUCCESS> => ({
  type: LoginUserActionEnum.LOGOUT_USER_SUCCESS,
  payload: {}
});

export const createUserAction = (user: UserModel): AppAction<LoginUserActionEnum.CREATE_USER> => ({
  type: LoginUserActionEnum.CREATE_USER,
  payload: {user}
});

export const createUserSuccessAction = (user): AppAction<LoginUserActionEnum.CREATE_USER_SUCCESS> => ({
  type: LoginUserActionEnum.CREATE_USER_SUCCESS,
  payload: {user}
});

export const checkUserTokenAction = (token: string): AppAction<LoginUserActionEnum.CHECK_USER_TOKEN> => ({
  type: LoginUserActionEnum.CHECK_USER_TOKEN,
  payload: {token}
});

export type AllLoginAction =
  | ReturnType<typeof loginUserAction>
  | ReturnType<typeof loginUserSuccessAction>
  | ReturnType<typeof logoutUserAction>
  | ReturnType<typeof logoutUserSuccessAction>
  | ReturnType<typeof createUserAction>
  | ReturnType<typeof createUserSuccessAction>
  | ReturnType<typeof checkUserTokenAction>;
