import {Component, OnInit} from '@angular/core';
import {select} from '@angular-redux/store';
import {Observable} from 'rxjs';
import {userDisplayNameSelector} from './redux/login/login.selector';
import {ImLoginUserService} from './services/im-login-user.service';
import { MAIN_MENU_MODEL } from './static/constants';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent implements OnInit {
  isCollapsed = false;
  public mainMenuModel = MAIN_MENU_MODEL;
  @select(userDisplayNameSelector) displayName$: Observable<string>;

  constructor(private imLoginUserService: ImLoginUserService) {
  }

  ngOnInit(): void {
    this.imLoginUserService.autoUserLogin();
  }
}
