import {combineEpics} from 'redux-observable';
import {Injectable} from '@angular/core';
import {LoginUserEpic} from '../login/login.epic';

@Injectable()
export class EpicsService {
  constructor(private loginUserEpic: LoginUserEpic) {
  }

  getEpics() {
    return combineEpics(
      ...this.loginUserEpic.allEpic,
    );
  }
}
