import {Component, OnInit} from '@angular/core';
import {NzFormatEmitEvent, NzTreeNode} from 'ng-zorro-antd/tree';
import {NzContextMenuService, NzDropdownMenuComponent} from 'ng-zorro-antd/dropdown';
import {FormControl, FormGroup, Validators} from '@angular/forms';

@Component({
  selector: 'app-categories',
  templateUrl: './categories.component.html',
  styleUrls: ['./categories.component.less']
})
export class CategoriesComponent implements OnInit {
  activatedNode?: NzTreeNode;
  form: FormGroup;
  addCategory = false;
  value = '';
  subMenuCategory = [
    {
      name: 'Добавить подкатегорию',
      type: 'addSubCategory'
    },
    {
      name: 'Переименовать',
      type: 'renameCategory'
    },
    {
      name: 'Удалить подкатегорию',
      type: 'deleteCategory'
    }];
  subMenuSubCategory = [];

  nodes = [
    {
      title: 'parent 0',
      key: '100',
      expanded: true,
      children: [
        {title: 'leaf 0-0', key: '1000', author: 'NG ZORRO', isLeaf: true},
        {title: 'leaf 0-1', key: '1001', author: 'NG ZORRO', isLeaf: true}
      ]
    },
    {
      title: 'parent 1',
      key: '101',
      children: [
        {title: 'leaf 1-0', key: '1010', author: 'NG ZORRO', isLeaf: true},
        {title: 'leaf 1-1', key: '1011', author: 'NG ZORRO', isLeaf: true}
      ]
    }
  ];

  constructor(private nzContextMenuService: NzContextMenuService) {
  }

  ngOnInit(): void {
    this.form = new FormGroup({
      addCategory: new FormControl('', [Validators.required])
    });
  }

  openFolder(data: NzTreeNode | NzFormatEmitEvent): void {
    // do something if u want
    if (data instanceof NzTreeNode) {
      data.isExpanded = !data.isExpanded;
    } else {
      const node = data.node;
      if (node) {
        node.isExpanded = !node.isExpanded;
      }
    }
  }

  activeNode(data: NzFormatEmitEvent): void {
    this.activatedNode = data?.node!;
  }

  contextMenu($event: MouseEvent, menu: NzDropdownMenuComponent): void {
    this.nzContextMenuService.create($event, menu);
  }

  selectDropdown(action): void {
    switch (action) {
      case 'value1':
      case 'value2':
      default:
    }
  }

  createNewCategory() {
    if (this.addCategory) {
      this.form.get('addCategory').markAsDirty();
      this.form.get('addCategory').updateValueAndValidity();
      if (this.form.valid) {
        this.addCategory = !this.addCategory;
        this.nodes = [{
          title: this.form.get('addCategory').value,
          key: '102',
          children: []
        }, ...this.nodes];
        this.form.get('addCategory').reset();
      }
    } else {
      this.addCategory = !this.addCategory;
    }
  }

}
