import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {PROXY_SERVER} from '../../config-service';
import {Observable} from 'rxjs';
import {LoginUserModel, UserModel} from '../model/im-user.model';
import {NgRedux} from '@angular-redux/store';
import {AppState} from '../redux';
import {checkUserTokenAction} from '../redux/login/login.actions';

@Injectable()
export class ImLoginUserService {

  constructor(private http: HttpClient,
              private ngRedux: NgRedux<AppState>) {
  }

  public userLogin(user: UserModel): Observable<LoginUserModel> {
    if (user) {
      return this.http.post<LoginUserModel>(`${PROXY_SERVER}/loginUser`, user);
    }
  }

  public userLogout(): Observable<LoginUserModel> {
    const token = sessionStorage.getItem('token');
    if (token) {
      return this.http.post<LoginUserModel>(`${PROXY_SERVER}/logoutUser`, {token});
    }
  }

  public createUser(user: UserModel): Observable<LoginUserModel> {
    if (user) {
      return this.http.post<LoginUserModel>(`${PROXY_SERVER}/createUser`, user);
    }
  }

  public checkUser(token: string): Observable<LoginUserModel> {
    if (token) {
      return this.http.get<LoginUserModel>(`${PROXY_SERVER}/checkUserToken`);
    }
  }

  public addHeaderInSessionStorage(header) {
    sessionStorage.setItem('token', header);
  }

  public removeHeaderInSessionStorage() {
    sessionStorage.removeItem('token');
  }

  public autoUserLogin() {
    const tokenInStorage = sessionStorage.getItem('token');
    if (tokenInStorage) {
      this.ngRedux.dispatch(checkUserTokenAction(tokenInStorage));
    }
  }
}
